/* Book.css */

.buttons-container{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.page:nth-child(even) .s-page{
  /* background-color: #b70000; */
padding-left: 50px;
  /* flex-direction: column-reverse; */
}
.page:nth-child(odd) .s-page{
  /* background-color: #b70000; */
padding-left: 50px;
  flex-direction: column-reverse;
}
.r{
    transform: rotate(180deg);
   

}
.s{
    transform: scale(.7);
    
  
}
.flipbook-container {
    /* width: 400px;
    height: 600px; */
    background-color: #f2f2f2;
    border: 1px solid #ddd;
  }
  .s-page{
display: flex;
flex-direction: column;
gap:30px;
  }
  .txt-container{
font-size: 1.5rem;
width: 80%;
  }
  .img-container{
display: flex;
/* border-radius: 10p; */
width:100%;
position: relative;
margin-top: 40px;
justify-content: center;
align-items: center;
  }
  .img
  {
    width:70%;
    border-radius: 10px;
    height: 80%;
    transform: rotate(4deg);
    margin-left: -60px;
  }
  .tape{
    width:30%;
    height:20%;
    position: absolute;
    top:0;
    transform: translateX(15%) translateY(25%) rotateZ(-30deg);
    left:0;
    z-index: 10;
    background-color: #ffcc0085;

  }
  .titlePg{
    background-image: url('https://res.cloudinary.com/sidd293/image/upload/v1685095545/Children-book-cover-page-2-CRC_csayfg.png');
    background-size: cover;
  }
  .ltape{
    width:30%;
    height:20%;
    position: absolute;
    top:80%;
    /* left:500px; */
    /* bottom: 0; */
    transform: translateX(200%) translateY(-30%) rotateZ(-30deg);
    left:0;
    z-index: 10;
    background-color: #ffcc0078;

  }
